.mainCard{
    margin-left: 30px;
    border-radius: 10px;

padding: 0;
}

.mainCard div{

    top: 0;
    color:white;
    padding: 8px;
    width: 75%;

}

.mainCard h2{
    color:#fab40d;
    font-weight:700;
    font-size: 40px;
    text-align: start;
}

.mainCard p{
    color:#696d70;
    font-weight:500;
    font-size: 17px;
    text-align: justify;
}

.mainCard .instructor{
    display: flex;
    color:#696d70;
    gap:10px;
    align-items: center;
    font-size: 14px;
   
}


.mainCard .instructor img{
    max-width: 70px;
    max-height: 70px;
    border-radius: 100%;
  
}

.mainCard .completion{
    display: flex;
    width: 100%;
    border-radius: 20px;
    justify-content: space-evenly;
    align-items: center;

}

.mainCard .percentage{
    font-size: 18px;
    font-weight: 700;
    background-color: var(--secondhalf);
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}


.mainCard .available{
    width: fit-content;
    height: fit-content;
    opacity: 0.8;
    padding: 0;
    margin-left: 10px;
}

.mainCard .available p{
    font-size: 15px;
    margin: 0;
    padding: 0;
}