:root{
  --background-color:#ffffff;
  --card-background:rgba(31,33,48,255);
  --firsthalf:#ffffff;
  --secondhalf:#f3c9f6;
}

body{
background-color: var(--background-color);
overflow-x: hidden;
font-family: 'Sora', sans-serif;

}

ul{
  list-style: none;
}

svg{
  position: absolute;
  bottom: 0;
  left:0;
}

.App{
  display: grid;
  grid-template-columns: 50% 50%;
width: 100%;
height: 100vh;
}

.firstHalf{
  background-color: var(--firsthalf);
  z-index: 10;
}

.secondHalf{
  background-color: var(--secondhalf);
  z-index: 10;
  position: relative;
  width: 100%;
  position: relative;
}


.container-wrap{
position: absolute;
top:-20px;
left:-60px;
width: 100%;

}