.container{
    display: grid;
   grid-template-columns: 50% 50% 50%;
   align-self: center;
   grid-auto-flow: column;
   overflow-x: visible;
   margin-top: 120px;
   animation: slide 0.5s ease-in-out forwards;
}



@keyframes slide{
    0%{
        margin-right: 30px;
    }
    100%
    {
        margin-right: 0px;
    }
}

.cardContainerMain button{
    margin-top: 30px;
border: none;
outline: none;
padding: 12px 30px;
background-color: #6d7278;
border-top-left-radius: 10px;
border-bottom-left-radius: 10px;
}

.cardContainerMain button img{
    max-width: 30px;
    max-height:30px;
}