.card{
    position: relative;
    max-width: 280px;
    height: 350px;

    border-radius: 10px;
    -webkit-box-shadow: 3px 3px 5px 0px rgba(0,0,0,0.75);
-moz-box-shadow: 3px 3px 5px 0px rgba(0,0,0,0.75);
box-shadow: 3px 3px 5px 0px rgba(0,0,0,0.75);
}

.card::after{
    content: '';
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background: rgb(61,190,198);
background: -moz-linear-gradient(0deg, rgba(61,190,198,1) 0%, rgba(255,255,255,1) 82%);
background: -webkit-linear-gradient(0deg, rgba(61,190,198,1) 0%, rgba(255,255,255,1) 82%);
background: linear-gradient(0deg, rgba(61,190,198,1) 0%, rgba(255,255,255,1) 82%);
filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#3dbec6",endColorstr="#ffffff",GradientType=1);
border-radius: 10px;
opacity: 0.14;
    z-index: -3;
}

.card div{
    position: absolute;
    bottom: 0;
    /* background-color: rgba(107,72,105,255); */
  
    color:white;
    padding: 8px;
}


.card div::after{
    content: '';
    position: absolute;
    top:0;
    left:0;
    width: 100%;
    height: 100%;
    background-color: rgba(107,72,105,255);
    z-index: -2;
    opacity: 0.72;
    border-radius: 10px;
}

.card img{
    position: absolute;
    top:0;
    left:0;
    z-index:-4;
    max-width: 100%;
    max-height: 100%;
}

