.search {
  
    z-index: 20;
    position: absolute;
    top:0;
    right: 0;
}

.search form{
    display: flex;
    border-top-left-radius: 20px;
    border-bottom-left-radius: 20px;
    padding:5px;
    background-color: #ffd768;
    margin-top: 24px;
    justify-content: center;
    align-items: center;
}

.search img{
max-width: 30px;
max-height: 30px;
}

.search input{
border:none;
outline: none;
font-size: 16px;
padding-left: 8px;
letter-spacing: 0.5px;
    background-color: #ffd768;
    height: 22px;
    color:rgb(93, 87, 85)
}